<template>
  <div class="larkPlayer">
    <div id="container"></div>
    <button @click="btnclick()">切换标清m3u8</button>
    <button @click="btnclick1()">切换map4</button>
    <div v-text="naoTime"></div>
    <div v-text="playLastTime"></div>
  </div>
</template>

<script>
import larkplayer from 'larkplayer'
import 'larkplayer-ui'
import 'larkplayer-hls'
// import '../plugins/hello'
// import '../plugins/pause'

export default {
  data () {
    return {
      player: null,
      naoTime: '',
      lastTime: 300,
      playTime: 0,
      playLastTime: ''
    }
  },
  mounted () {
    if (!this.player) {
      this.init()
    }
  },
  methods: {
    init () {
      const _this = this
      let width = Math.min(document.body.clientWidth, 640)
      _this.$nextTick(() => {
        _this.player = larkplayer('container', {
          width: width,
          height: width * 9 / 16,
          controls: true,
          autoplay: true,
          playsinline: true,
          preload: true,
          // src: 'https://baikebcs.bdimg.com/baike-other/big-buck-bunny.mp4'
          source: [{
            src: 'https://baikebcs.bdimg.com/baike-other/big-buck-bunny.mp4',
            type: 'video/mp4'
          }
          // ,{
          //   src: 'https://look.chinahrt.com.cn/courseyun/rx/transcode/20206/317690bd-f600-43a0-87a7-6c6182da4607/283003-m3u8.m3u8',
          //   type: 'application/x-mpegURL'
          // },
          // {
          //   src: 'https://look.chinahrt.com.cn/u/courseyun/7d1e9d587bc648cbbdb869c1300967f8/transcode/20216/5cf04cc0-2d74-4ae2-ab10-3ee59d4388a5/283006-mp4.mp4',
          //   type: 'video/mp4'
          // }
          ]
          // ,
          // UI: {
          //   // 插件名称默认为首字母小写的类名
          //   // 可通过 Component.register(ErrorDialog, {name: 'another_name'}) 指定名称
          //   errorDialog: {}
          // }
          // ,
          // plugin: {
          //   hello: {}
          // }
        }, function () {
          let imgUrl = 'https://img.alicdn.com/tfs/TB1byi8afDH8KJjy1XcXXcpdXXa-1920-514.jpg'
          let url = 'https://baidu.com'
          let video = document.getElementById('container-video-larkplayer')
          let videoDiv = document.getElementsByTagName('video')[0]
          videoDiv.setAttribute('x5-playsinline', '')
          videoDiv.setAttribute('webkit-playsinline', 'true')
          videoDiv.setAttribute('x-webkit-airplay', 'allow') // 禁止ios自动全屏
          videoDiv.setAttribute('x5-video-player-type', 'h5') // 启用H5内核H5播放器

          let div = document.createElement('div')
          div.id = 'AD-video'
          div.style.display = 'none'
          div.innerHTML = '<span class="ad-text ad-span">广告</span>' +
          '<span class="ad-close ad-span" onclick="var AD = document.getElementById(\'AD-video\');AD.style.display = \'none\'">x</span>' +
          '<img src="' + imgUrl + '" width="100%" height="100%" ' +
          'onclick="window.open(\'' + url + '\')">'
          video.appendChild(div)
        })
        // _this.player.on('play', function (e) {
        //   if (_this.lastTime > 0) {
        //     _this.player.currentTime(_this.lastTime)
        //   }
        // })
        // _this.player.on('seeking', function (e) {
        //   isSeek = true
        //   // startT = timeNow
        //   // console.log(33, _this.player.currentTime(), startT)
        //   startT = _this.player.currentTime()
        // })
        // _this.player.on('seeked', function (e) {
        //   if (startT - _this.lastTime > 2) {
        //     // alert('快进了')
        //     _this.naoTime = '当前时间：' + _this.lastTime
        //     _this.player.currentTime(_this.lastTime)
        //   } else {
        //     if (isSeek) {
        //       console.log(startT)
        //     }
        //     // _this.player.currentTime(startT)
        //   }
        //   isSeek = false
        //   // if (_this.lastTime === 0) {
        //   //   if (_this.player.currentTime() - timeNow > 2) {
        //   //     // alert('快进了')
        //   //     _this.naoTime = '当前时间：' + timeNow
        //   //     _this.player.currentTime(timeNow)
        //   //   }
        //   // } else {
        //   //   console.log(55, _this.player.currentTime())
        //   // }
        // })
        // eslint-disable-next-line no-unused-vars
        let timeNow = 0
        let isSeek = false
        // eslint-disable-next-line no-unused-vars
        let startT = 0
        _this.player.on('timeupdate', function (e) {
        // console.log(33,player.currentTime());
          if (!isSeek) {
            timeNow = _this.player.currentTime()
            _this.playTime = timeNow
            // _this.lastTime = _this.player.currentTime()
            // console.log('timeNow', timeNow)
          }
        })

        _this.player.on('seeking', function (e) {
          isSeek = true
          startT = timeNow
          // console.log(33, _this.player.currentTime(), startT)
        })
        _this.player.on('seeked', function (e) {
          console.log(55, _this.player.currentTime())
          if (_this.player.currentTime() - timeNow > 2) {
            // alert('快进了')
            _this.naoTime = '当前时间：' + timeNow
            _this.player.currentTime(timeNow)
          }
          isSeek = false
        })
        /**
         * @description: 暂停事件
         */
        _this.player.on('pause', function (e) {
          let AD = document.getElementById('AD-video')
          AD.style.display = 'block'
        })
        /**
         * @description: 播放事件
         */
        _this.player.on('play', function (e) {
          let AD = document.getElementById('AD-video')
          AD.style.display = 'none'
        })
        /**
         * @description: 播放事件
         */
        _this.player.on('error', function (e) {
          console.log(222)
        })
      })
    },
    btnclick () {
      this.player.src('https://look.chinahrt.com.cn/courseyun/rx/transcode/20206/317690bd-f600-43a0-87a7-6c6182da4607/283003-m3u8.m3u8')
      // this.playLastTime = '上次播放时间：' + this.playTime
      this.player.currentTime(this.playTime)
      const _info = this
      if (this._isMobile()) {
        let videoDiv = document.getElementsByTagName('video')[0]
        videoDiv.addEventListener('loadedmetadata', function () {
          this.currentTime = _info.playTime
        }, false)
      }
      this.player.play()
    },

    btnclick1 () {
      this.player.src('https://look.chinahrt.com.cn/u/courseyun/7d1e9d587bc648cbbdb869c1300967f8/transcode/20216/5cf04cc0-2d74-4ae2-ab10-3ee59d4388a5/283006-mp4.mp4')
      // this.playLastTime = '上次播放时间：' + this.playTime
      this.player.currentTime(this.playTime)
      const _info = this
      if (this._isMobile()) {
        let videoDiv = document.getElementsByTagName('video')[0]
        videoDiv.addEventListener('loadedmetadata', function () {
          this.currentTime = _info.playTime
        }, false)
      }
      this.player.play()
    },
    /**
     * @description: 判断是否是手机
     * @return {*} 访问端pc/手机
    */
    _isMobile () {
      let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
      return flag
    }
  }
}
</script>

<style>
* { touch-action: pan-y; }
#container-video-larkplayer video {
  position: relative;
}
#AD-video {
  position: absolute;
  width: 60%;
  height: 50%;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}
#AD-video .ad-span {
  position: absolute;
  background-color: #333;
  color: #fff;
  padding: 3px 5px;
  font-size: 14px;
}
#AD-video .ad-text {
  left: 5px;
  top: 5px;
}
#AD-video .ad-close {
  padding: 2px 7px;
  font-size: 16px;
  top: 5px;
  right: 5px;
}
</style>



